.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0 50px;

  &__img {
    width: 40px;
    height: 40px;

    & img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    list-style: none;

    & + & {
      margin-left: 20px;
    }
  }

  &__link {
    text-transform: uppercase;
    transition: color 0.3s ease 0s;
    padding: 22px 0;
    text-decoration: none;
    color: inherit;

    &:hover {
      color: $blue-color;
    }
  }
}

.is-active {
  position: relative;
  color: $blue-color;
  padding: 23px 0;

  &::after {
    content: '';
    background-color: $blue-color;
    width: 100%;
    height: 4px;
    border-radius: 8px;
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}
