.card {
  box-sizing: border-box;
  padding: 32px 16px 15px;
  width: 200px;
  border-radius: 5px;
  background-color: $color-white;
  color: $color-main;
  border: 1px solid $border-color;
  transition: transform 0.3s ease 0s;

  &:hover {
    transform: scale(1.2);
  }
  &:hover &__title {
    color: $title-hover;
  }

  &__image {
    padding-inline: 3px;
    margin-bottom: 36px;
    & img {
      width: 100%;
      height: 134px;
      background-size: cover;
    }
  }

  &__title {
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    margin-bottom: 4px;
    color: $color-bold;
  }

  &__code {
    font-size: 10px;
    font-weight: 400;
    margin: 0;
    line-height: 14px;
    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    font-weight: 400;
  }

  &__quantity-reviews {
    font-size: 10px;
    line-height: 14px;
    align-self: end;
    margin: 0;
    font-weight: 400;
    color: $color-bold;
  }

  &__price {
    display: flex;
    justify-content: space-between;
  }
  &__descr {
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 16px;
    font-weight: 400;
  }

  &__value {
    font-weight: 700;
    font-size: 16px;
    margin: 0;
    color: $color-bold;
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 166px;
    height: 40px;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    background-color: $blue-color;
    box-sizing: border-box;
    border: 1px solid $blue-color;
    border-radius: 5px;
    color: $color-white;
    text-decoration: none;
    transition: all 0.3s ease-in-out 0s;

    &:hover {
      background-color: $color-white;
      color: $blue-color;
    }
  }
}
